import { SetShippingMethodsOnCartInput } from '@/checkout/__generated__/types';
import { MissingCart } from '@/checkout/api';
import { setShippingMethodsMutation } from '@/checkout/api/setShippingMethods/graphql/setShippingMethodsOnCart.graphql';
import { dispatchApiCall } from '@/checkout/api/utils/dispatchApiCall';
import { transformCart } from '@/checkout/data/transforms/index';

export type SetShippingMethodsOnCartInputs = {
  cartId: string;
  shippingMethods: SetShippingMethodsOnCartInput['shipping_methods'];
};

export const setShippingMethodsOnCart = async ({
  cartId,
  shippingMethods,
}: SetShippingMethodsOnCartInputs) => {
  if (!cartId) throw new MissingCart();

  return dispatchApiCall({
    type: 'mutation',
    query: setShippingMethodsMutation,
    options: { variables: { cartId, shippingMethods } },
    path: 'setShippingMethodsOnCart.cart',
    signalType: 'cart',
    transformer: transformCart,
  });
};
