import {
  CART_DATA_FRAGMENT,
  CART_SUMMARY_FRAGMENT,
} from '@/checkout/api/getCart/graphql/getCart.graphql';

export const setShippingMethodsMutation = /* GraphQL */ `
  mutation setShippingMethods(
    $cartId: String!
    $shippingMethods: [ShippingMethodInput]!
  ) {
    setShippingMethodsOnCart(
      input: { cart_id: $cartId, shipping_methods: $shippingMethods }
    ) {
      cart {
        id
        ...CartData
        ...CartSummaryItems
      }
    }
  }
  ${CART_DATA_FRAGMENT}
  ${CART_SUMMARY_FRAGMENT}
`;
